import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home'
import Layout from '../components/Layout'
Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        component: Layout,
        children: [
            {
                path: '/',
                name: 'Home',
                component: Home
            }
        ]
    },
]

const router = new VueRouter({
    mode: 'hash',
    routes
})

export default router
